#root {
	background-color: #000;
}

.landingHeader{
  z-index: 1;
  position: relative;
  text-align: center;
  padding-top: 40vh;
}

.button{
  background-color: #2f54eb;
  color: white;
  border: 2px solid #597ef7;
  border-radius: 5px;
  padding: 5px 10px;
  margin-top: 10px;
}


.text{
	color: white;
}

.slideshow{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  list-style: none;
  margin: 0;
  padding: 0;
}

.listStyle{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0;
  z-index: 0;
  animation: imageAnimation 50s linear infinite;
}

.list1{
  background-image: url("../images/LandingPage/lanborghini.png");
}

.list2{
  background-image: url("../images/LandingPage/Audi.png");
  animation-delay: 10s;
}

.list3{
  background-image: url("../images/LandingPage/BMW.png");
  animation-delay: 20s;
}

.list4{
  background-image: url("../images/LandingPage/ferrari.png");
  animation-delay: 30s;
}

.list5{
  background-image: url("../images/LandingPage/lanbor2.png");
  animation-delay: 40s;
}

@keyframes imageAnimation {
  0% {
    opacity: 0;
    animation-timing-function: ease-in;
  }
  10% {
    opacity: 1;
    animation-timing-function: ease-out;
  }
  20% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
}
